import { useContext } from 'react'
import ContadorContext from './ContadorContext' // Importa tu contexto actual
import espaolWhite from './../assets/images/index/espaolWhite.png' // Icono del sol
import espaolBlack from './../assets/images/index/espaolBlack.png' // Icono de la luna
import inglesWhite from './../assets/images/index/inglesWhite.png' // Icono del sol
import inglesBlack from './../assets/images/index/inglesBlack.png' // Icono de la luna
const ThemeLenguaje = () => {
	const { lenguaje, theme, toggleLenguaje } = useContext(ContadorContext) // Accede al estado y la funcin

	return (
		<div className="lenguaje">
			<button onClick={toggleLenguaje}>
				<figure className={lenguaje === 'Espaol' ? 'sombra' : null}>
					<img src={theme === 'dark' ? espaolBlack : espaolWhite} alt="Switch lenguaje" />
				</figure>
				<figure className={lenguaje === 'Ingles' ? 'luz' : null}>
					<img src={theme === 'dark' ? inglesBlack : inglesWhite} alt="Switch lenguaje" />
				</figure>
			</button>
		</div>
	)
}

export default ThemeLenguaje
